@media screen and (max-width: 755px) {
    canvas {
        position: absolute;
        top: 0;
        z-index: -1;
    }
}
@media screen and (min-width: 756px) {
    canvas {
        position: absolute;
        background-color: black;
        top: 0;
        z-index: 0;
        border: 1px solid black;
        pointer-events: all;
    }
}
body {
    overflow-x: hidden;
    overflow-y: hidden;
    border: 0;
    padding: 0;
    margin: 0;
    .personal {
        pointer-events: none;
        position: relative;
        .personal-container {
            position: relative;
            z-index: 1;
            width: 100vw;
            // height: 65vh;

            h1 {
                position: relative;
                z-index: 1;
                font-size: 5vw;
                color: white;
                text-align: center;
                margin-bottom: 1vw;
            }
            .img-hobbies {
                margin: 0;
                display: grid;
                grid-template-rows: repeat(auto-fit, minmax(250px, 1fr));
                img {
                    position: relative;
                    z-index: 1;
                    justify-self: center;
                    width: 45vw;
                    height: 45vw;
                }
                .others {
                    position: relative;
                    text-align: left;
                    font-size: 3vw;
                    padding: 25px 2%;
                    color: white;
                }
            }
            @media screen and (min-width: 755px) {
                .img-hobbies {
                    margin: 0;
                    display: grid;
                    grid-template-columns: repeat(auto-fit, minmax(40px, 1fr));
                    img {
                        pointer-events: all;
                        z-index: 1;
                        justify-self: center;
                        width: 35vw;
                        height: 35vw;
                    }
                    p {
                        text-align: left;
                        font-size: 2.5vw;
                        padding: 0 2%;
                        color: white;
                    }
                }
            }
        }
        .contacts {
            position: relative;
            margin: 0;
            display: flex;
            justify-content: center;
            align-items: center;

            div {
                pointer-events: all;
                margin: 0;
                border: 0;
                padding: 3vw;

                a {
                    margin: 0;
                    width: inherit;
                    height: inherit;
                }
                .icon {
                    position: relative;
                    z-index: 1;
                    width: 7.5vw;
                    height: 7.5vw;
                }
                .hover {
                    visibility: hidden;
                    position: absolute;
                    margin: 0;
                    opacity: 0;
                    overflow: hidden;
                    bottom: 12.5vw;
                    a {
                        text-decoration: none;
                    }
                }

                .facebook {
                    background-color: #242526;
                    font-family: sans-serif;
                    width: 295px;
                    height: 165px;
                    padding: 0;
                    border-radius: 15px;
                    box-shadow: 0px 0px 7px 1px #eee;
                    display: flex;
                    flex-direction: column;
                    transition-delay: 0.7s;

                    a {
                        width: inherit;
                        height: inherit;
                    }
                    .border {
                        width: inherit;
                        height: 60%;
                        padding: 10px;
                        display: flex;
                        flex-direction: row;

                        img {
                            position: relative;
                            border-radius: 50%;
                            margin: 0;
                            margin-right: 15px;
                            padding: 0;
                            width: 90px;
                            height: 90px;
                        }
                        .text {
                            padding: 0;
                            width: 180px;

                            p {
                                display: flex;
                                flex-direction: row;
                                align-items: center;
                                color: #d9d9d9;

                                img {
                                    width: 20px;
                                    height: 20px;
                                    border-radius: 0;
                                }
                            }

                            h1 {
                                color: #d9d9d9;
                                font-weight: bold;
                                font-size: 18px;
                                padding: 0;
                            }
                        }
                    }
                    .buttons {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        padding: 0;

                        .buttoning {
                            width: 150px;
                            height: 40px;
                            background-color: #3a3b3c;
                            border-radius: 5px;
                            display: flex;
                            align-items: center;
                            justify-content: center;

                            label {
                                color: white;
                                font-size: 15px;
                                font-weight: 600;
                            }

                            img {
                                position: relative;
                                filter: invert(1);
                                margin-right: 5px;
                            }
                        }
                        .buttoning:hover {
                            background-color: #4e4f50;
                        }
                        .buttoning:active {
                            transform: scale(0.85);
                        }
                    }
                }
                .facebook:hover {
                    visibility: visible;
                    opacity: 1;
                    z-index: 1;
                    cursor: pointer;
                }

                .instagram {
                    font-family: sans-serif;
                    width: 400px;
                    height: 135px;
                    padding: 0;
                    background-color: #eee;
                    border-radius: 15px;
                    box-shadow: 0px 0px 7px 1px #888;
                    transition-delay: 0.7s;
                    a {
                        width: inherit;
                        height: inherit;
                        color: black;
                    }
                    .border {
                        padding-right: 15px;
                        width: inherit;
                        height: inherit;
                        padding: 0;
                        display: flex;
                        flex-direction: row;

                        img {
                            align-self: center;
                            border-radius: 50%;
                            margin: 0 10px;
                            padding: 0;
                            width: 100px;
                            height: 100px;
                        }
                        .descriptions {
                            padding: 0;
                            position: relative;
                            display: flex;
                            flex-direction: column;
                            justify-content: space-around;
                            .count {
                                color: #262626;
                                font-weight: 600;
                                font-size: 17px;
                                padding: 0;

                                label {
                                    font-weight: normal;
                                    font-size: 15px;
                                }
                            }
                            .bioname {
                                color: #262626;
                                font-weight: 600;
                                font-size: 17px;

                                label {
                                    font-weight: normal;
                                    color: black;
                                    font-size: 13px;
                                }
                            }
                            .username {
                                font-family: "Quicksand", sans-serif;
                                font-size: 24px;
                                font-weight: normal;
                            }
                        }
                    }
                }
                .instagram:hover {
                    visibility: visible;
                    opacity: 1;
                    z-index: 1;
                    cursor: pointer;
                }
                .twitter {
                    font-family: sans-serif;
                    width: 265px;
                    height: 165px;
                    padding: 0;
                    background-color: black;
                    border-radius: 15px;
                    box-shadow: 0px 0px 7px 1px #eee;
                    transition-delay: 0.7s;

                    .border {
                        padding: 10px;

                        p {
                            color: #e4e6eb;
                            font-weight: bold;
                            font-size: 17px;
                            padding: 0;
                        }

                        img {
                            border-radius: 50%;
                            position: relative;
                            margin: 0;
                            padding: 0;
                            width: 25%;
                            height: 25%;
                        }

                        div {
                            padding: 0;
                            padding-top: 5px;
                        }
                        label {
                            padding: 0;
                            font-weight: normal;
                            font-size: 13px;
                            color: #6e767d;
                        }
                        .lockKey {
                            margin-left: 2.5px;
                            position: absolute;
                            width: 18px;
                            height: 18px;
                            fill: white;
                        }
                    }
                }
                .twitter:hover {
                    visibility: visible;
                    opacity: 1;
                    z-index: 1;
                    cursor: pointer;
                }
                .gmail {
                    font-family: sans-serif;
                    width: 235px;
                    height: 35px;
                    padding: 0;
                    background-color: black;
                    border-radius: 5px;
                    box-shadow: 0px 0px 7px 1px #eee;
                    text-align: center;
                    transition-delay: 0.7s;

                    p {
                        padding: 0;
                        padding-top: 8px;
                        color: #d9d9d9;
                        font-weight: bold;
                        font-size: 17px;
                    }
                }
                .gmail:hover {
                    visibility: visible;
                    opacity: 1;
                    z-index: 1;
                    cursor: auto;
                }
                .hovering {
                    visibility: visible;
                    opacity: 1;
                    z-index: 1;

                    .noneTwitter {
                        padding: 0;
                        padding-top: 10px;
                        text-align: center;

                        p {
                            color: white;
                            font-weight: normal;
                        }
                    }

                    .noneGmail {
                        padding: 0;
                        text-align: center;

                        p {
                            color: white;
                            font-weight: normal;
                        }
                    }

                    .noneFacebook {
                        padding: 0;
                        padding-top: 10px;
                        text-align: center;

                        p {
                            color: white;
                            font-weight: normal;
                        }
                    }
                    .noneInstagram {
                        padding: 0;
                        padding-top: 10px;
                        text-align: center;

                        p {
                            color: black;
                            font-weight: normal;
                        }
                    }
                }
            }
        }
    }
}
.cursor {
    display: none;
    z-index: 2;
    position: absolute;
    width: 70px;
    height: 70px;
    border-radius: 50px;
    backdrop-filter: brightness(1.5);
    transform: translate(6vw, 5.2vw);
}
