.loadStyle {
    pointer-events: none;
    z-index: 15;
    position: fixed;
    width: calc(10px + 100vw);
    height: 100vh;
    top: 0px;
    left: 0px;
    transition: opacity 2s ease-out;
}
.loadUp {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 53%;
    background-color: #222222;
    transition: all 1.5s ease-out;
}
.loadDown {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 53%;
    background-color: #222222;
    transition: all 1.5s ease-out;
}
.circleMiddle {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: all 1.5s ease-out;

    .biggestCircle {
        position: absolute;
        z-index: 17;
        width: 170px;
        height: 170px;
        border-radius: 50%;
        border: 5px solid rgba($color: #222222, $alpha: 0);
        border-top-color: #e24745;
        animation: moveCircles 0.8s infinite normal;
    }
    .biggerCircle {
        position: absolute;
        z-index: 17;
        width: 140px;
        height: 140px;
        border-radius: 50%;
        border: 5px solid rgba($color: #222222, $alpha: 0);
        border-top-color: #efed50;
        animation: moveCircles 1.5s infinite normal;
    }
    .bigCircle {
        position: absolute;
        z-index: 17;
        width: 110px;
        height: 110px;
        border-radius: 50%;
        border: 5px solid rgba($color: #222222, $alpha: 0);
        border-top-color: #46dc48;
        animation: moveCircles 2.3s infinite normal;
    }
    .mediumCircle {
        position: absolute;
        width: 80px;
        height: 80px;
        z-index: 17;
        border-radius: 50%;
        border: 5px solid rgba($color: #222222, $alpha: 0);
        border-top-color: #45e5ec;
        animation: moveCircles 3s infinite normal;
    }
    .smallCircle {
        position: absolute;
        width: 50px;
        height: 50px;
        z-index: 17;
        border-radius: 50%;
        border: 5px solid rgba($color: #222222, $alpha: 0);
        border-top-color: #4748ea;
        animation: moveCircles 4.5s infinite normal;
    }
}

@keyframes moveCircles {
    from {
        transform: rotate(1deg);
    }
    to {
        transform: rotate(360deg);
    }
}
