.memories-container {
    width: calc(10px + 100vw);
    height: calc(12px + 275vw);
    background-color: #555555;
    z-index: 100;

    .imgs-container {
        display: flex;
        flex-direction: column;
        justify-content: center;

        div[class^="pic"] {
            width: 95vw;
            height: 60vw;
            padding: 0 2.5vw;
            display: grid;
            grid-template-columns: 1fr 1fr;
            line-height: 0.5;

            span {
                position: relative;
                display: block;
                width: 40vw;
                margin-top: 5vw;
                height: 22vh;
                color: white;
                font-family: "Roboto", sans-serif;
                align-self: flex-start;

                h1 {
                    font-size: 4vw;
                    text-transform: uppercase;
                    line-height: 1;
                }

                .date {
                    position: relative;
                    font-size: calc(5px + 0.5vw);
                    font-weight: lighter;
                    font-style: italic;
                }
                .left-date {
                    text-indent: 15px;
                }
                .right-date {
                    margin-right: 15px;
                }
                .sentence {
                    position: relative;
                    font-size: 3vw;
                    margin-top: 2vw;
                    line-height: 1;
                    font-family: "Frank Ruhl Libre", serif;
                    font-weight: lighter;
                    text-transform: none;
                }
            }
            img {
                position: relative;
                margin: 4vh 0px;
                width: 50vw;
                height: 35vw;
                align-self: center;
            }
            .right {
                justify-self: right;
            }
            .right-text {
                text-align: right;
            }
        }
        .more {
            width: inherit;
            height: 22vh;
            display: flex;
            justify-content: center;
            align-self: center;

            button {
                position: relative;
                width: 22vh;
                height: 7vh;
                background-color: inherit;
                color: #51e2f5;
                border: 1px solid #51e2f5;
            }
            button:hover {
                color: #707070;
                border: #707070;
                background-color: #51e2f5;
            }
            button:active {
                color: #707070;
                border: #707070;
                background-color: #55c5d4;
            }
        }
    }
}
@media screen and (min-width: 768px) {
    .memories-container {
        width: 100vw;
        height: 225vw;
        background-color: #555555;
        z-index: 100;

        .imgs-container {
            display: flex;
            flex-direction: column;
            justify-content: center;

            div[class^="pic"] {
                width: 95vw;
                height: 50vw;
                padding: 0 2.5vw;
                display: grid;
                grid-template-columns: 1fr 1fr;

                span {
                    position: relative;
                    width: 40vw;
                    height: 60vh;
                    color: #eee;
                    font-family: "Roboto", sans-serif;
                    line-height: 1;
                    align-self: start;
                    margin-top: 30px;

                    h1 {
                        font-size: 4vw;
                        text-transform: uppercase;
                        line-height: 1;
                    }

                    .date {
                        display: block;
                        height: 10px;
                        position: relative;
                        font-size: calc(7px + 0.5vw);
                        font-weight: lighter;
                        font-style: italic;
                    }
                    .left-date {
                        text-indent: 25px;
                    }
                    .right-date {
                        margin-right: 25px;
                    }
                    .sentence {
                        margin-top: 15px;
                        position: relative;
                        font-size: 2.5vw;
                        line-height: 1;
                        font-family: "Frank Ruhl Libre", serif;
                        font-weight: lighter;
                        text-transform: none;
                    }
                }
                img {
                    position: relative;
                    width: 50vw;
                    height: 35vw;
                    align-self: center;
                }

                .right {
                    justify-self: right;
                }
                .right-text {
                    text-align: right;
                }
            }
            .more {
                width: inherit;
                height: 22vh;
                display: flex;
                justify-content: center;
                align-self: center;

                button {
                    position: relative;
                    width: 22vh;
                    height: 7vh;
                    background-color: inherit;
                    color: #51e2f5;
                    border: 1px solid #51e2f5;
                }
                button:hover {
                    color: #707070;
                    border: #707070;
                    background-color: #51e2f5;
                }
                button:active {
                    color: #707070;
                    border: #707070;
                    background-color: #55c5d4;
                }
            }
        }
    }
}
@import "./animate";
